import { createWebHistory, createRouter } from "vue-router";

const routes = [
    {
        path: '/',
        name: 'defaultPage',
        component: () => import('@/layout/DefaultPage'),   // 동적 import
        meta: {
            depth: 1,
        }
    },
    {
        path: '/UserInfoPage',
        name: 'UserInfoPage',
        component: () => import('@/layout/UserInfoPage'),
        meta: {
            depth: 1,
        }
    },
    {
        path: '/Test',
        name: 'TestPage',
        component: () => import('@/layout/TestPage'), 
        children: [
            {
                path: 'subPage01',  
                name: 'SubPage01',
                component: () => import('@/view/SubPage01'),
                meta: { 
                    depth: 1, 
                },
            },  
            {
                path: 'tabA',  
                name: 'TabView01',
                component: () => import('@/view/TabView01'),
                meta: { 
                    depth: 2, 
                },
            },  
            {
                path: 'tabB',  
                name: 'TabView02',
                component: () => import('@/view/TabView02'),
                meta: { 
                    depth: 3, 
                },
            },  
            {
                path: 'modalA',  
                name: 'ModalView01',
                component: () => import('@/view/ModalView01'),
                meta: { 
                    depth: 4, 
                },
            },
            {
                path: 'modalB',
                name: 'ModalView02',
                component: () => import('@/view/ModalView02'),
                meta: {
                    depth: 5,
                },
                children: [
                    {
                        path: 'type01',
                        name: 'MD01',
                        component: () => import('@/view/MD01'),
                        meta: {
                            depth: 6,
                        }
                    }
                ]
            },
            {
                path: 'accordion',  
                name: 'AccoView',
                component: () => import('@/view/AccoView'),
                meta: { 
                    depth: 6, 
                },
            }, 
            
        ]
    },
    {
        path: '/SelectBox',
        name: 'SelectBoxView',
        component: () => import('@/view/SelectBoxView'),
        meta: {
            depth: 1,
        }
    },
    {
        path: '/login',
        name: 'LoginPage',
        component: () => import('@/layout/LoginPage'),
        children: [
            {
                path: 'info/:id',
                name: 'LoginInfo',
                component: () => import('@/view/LoginInfo'),
                meta: {
                    depth: 1,
                }
            }
        ]
    },
    {
        path: '/study',
        name: 'StudyMemo',
        component: () => import('@/view/StudyMemo'),
    },
    {
        path: "/:catchAll(.*)*",
        component: () => import('@/layout/ErrorNotFound'),
        redirect: 'ErrorNotFound'
    },
];

export const router = createRouter({
    history: createWebHistory(),
    routes,
    scrollBehavior(to, from, savedPosition) {
       return savedPosition || new Promise ((resolve) => {
        setTimeout(() => resolve({ top: 0, behavior: 'smooth'}, 300 ))
       })
    },
});

